.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop {
  z-index: 3;
  background-color: var(--violet);
  transition: opacity var(--transition), visibility var(--transition);
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.8);
}

.modal {
  transition: transform var(--transition);
  background-color: var(--light-bg);
  border: 2px solid var(--black);
  border-radius: 18px;
  max-width: 335px;
  padding: 40px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.modal-close {
  transition: scale var(--transition);
  background-color: #0000;
  border: none;
  place-items: center;
  width: 20px;
  height: 20px;
  padding: 0;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

.modal-close:is(:hover, :focus) .close-icon {
  stroke: var(--violet);
}

.close-icon {
  stroke: var(--black);
  transition: stroke var(--transition);
  width: 20px;
  height: 20px;
}

